import React from 'react';

import './Home.css';
import headerImage  from '../assets/logo/codemastery.png'

const Home = () =>{
    return(
        <div className='containerWrapper'>
            <div className='containerInner'>
                <section>
                    <div className='heroSection'>
                        <div className='heading'>
                            <h2>Welcome to Codemastery</h2>
                        </div>
                        <div className='headerImage'>
                            <img src={headerImage}/>
                        </div>
                    </div>
                </section>
                <section>
                    <div className='toolCategories'>
                        <div className='headerName'>Tools Categories</div>
                        <div className='cardWrapper'>
                            <div className='cardBody'>
                                <div className='card'>
                                    <span>Text Tools</span>
                                </div>
                                <div className='card'>
                                    <span>Image Tools</span>
                                </div>
                                <div className='card'>
                                    <span>CSS Tools</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </div>
    )
}
export default Home;