import React from 'react';
import './Header.css';
import {Link} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars,faSearch } from '@fortawesome/free-solid-svg-icons'
import Logo from "../assets/logo/codemastery.png";

const Header = ()=>{
    return(
        <div className='headerWrapper'>
            <div className='headerInner'>
                <div className='logo'>
                    <img src={Logo} alt="Logo" />
                </div>
                <div className='searchBar'>
                    <div className='inputWrapper'>
                        <div className='inputField'>
                            <input type="text" placeholder='Search...'></input>
                        </div>
                        <div className='searchBtnWrapper'>
                            <a href="javascript:void(0)"><FontAwesomeIcon icon={faSearch}></FontAwesomeIcon></a>
                        </div>
                    </div>
                </div>
                <div className='navBar'>
                    <ul className='navItem'>
                        <li className='navList'><Link to="/">Home</Link></li>                        
                        <li className='navList'><Link to="/html">HTML</Link></li>
                        <li className='navList'><Link to="/css">CSS</Link></li>
                        <li className='navList'><Link to="/js">JS</Link></li>
                        <li className='navList'><Link to="/contactus">Contact Us</Link></li>
                        <li className='navList'><Link to="/profile">Profile</Link></li>
                    </ul>
                </div>
                <div className='toggleNavBar'>
                <FontAwesomeIcon icon={faBars} />
                </div>
            </div>            
        </div>
    )
}
export default Header;